import { Component, OnInit, AfterViewInit } from '@angular/core';
import {Chat} from './chat.model';
import {ChatService} from './chat.service';
import * as $ from 'jquery';

@Component({
    moduleId: module.id,
    selector: 'chat',
    templateUrl: 'chat.component.html'
})
export class ChatComponent implements OnInit, AfterViewInit {

    public messages = [];
    public msg = {
        n: "",
        m: ""
    };
    public isJoined: boolean;
    public ckCfg = {
        language: 'en',
        skin: 'office2013',
        allowedContent: true,
        entities: false,
        width: '790',
        enterMode: '',
        extraPlugins : 'smiley'
    };

    constructor(private _chatService: ChatService) { 
        if(localStorage.getItem('n')){
            this.isJoined = true;
            this.msg.n = localStorage.getItem('n');
        }else{
            this.isJoined = false;
            this.msg.n = '';
        }
    }

    ngOnInit() { 
        this._chatService.get().subscribe(message => {
            this.messages.push(message);
            $('.chat-box-content').animate({scrollTop: $('.chat-box-content').prop("scrollHeight")}, 'fast');
        })
    }

    onChat(){
        this._chatService.send(this.msg);
        this.msg.m = "";
    }

    onJoin(){
        localStorage.setItem('n', this.msg.n);
        this.isJoined = true;
    }

    ngAfterViewInit(){
        
    }

}


